/* media breakpoint mixin */
.wrapper:before, .lang__list, .header__search--desktop {
  opacity: 0;
  visibility: hidden;
}

.lang__list--open, body:has(.lang__list--open) .wrapper:before, body:has(.header__menu--open) .wrapper:before, .header__search--open {
  opacity: 1;
  visibility: visible;
}

