*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  text-decoration-skip: objects;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  min-width: 320px;
  overflow-x: hidden;
}

html {
  box-sizing: border-box;
}

html, body {
  min-height: 100%;
}

html, body {
  height: 100%;
}

audio,
video {
  display: block;
}

section {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:focus {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a img {
  border: none;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul, li {
  list-style-type: none;
}

img {
  max-width: 100%;
  display: block;
  border-style: none;
}

input,
textarea,
select {
  vertical-align: middle;
}

form, fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
  width: 100%;
}

input[type="search"],
input[type="text"],
input[type="file"],
input[type="password"],
input[type="submit"],
input[type="email"],
input[type="tel"],
textarea, select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  resize: none;
  outline: none;
}

input[type="email"] {
  box-shadow: none;
  -webkit-box-shadow: none;
}

input[type="image"] {
  padding: 0;
  border: none;
}

input[type="submit"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input[type="submit"],
input[type="button"] {
  cursor: pointer;
}

input[type="submit"]:focus,
input[type="button"]:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button,
input[type=email]::-webkit-inner-spin-button,
input[type=email]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

input::placeholder, input::-moz-placeholde, input::-webkit-input-placeholder {
  opacity: 0.5;
  color: inherit;
}

button:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

button {
  cursor: pointer;
  outline: none;
  border: none;
  box-shadow: none;
}

p, span, label, h1, h2, h3, h4, h5, h6, b, strong, i, em {
  max-width: 100%;
  word-wrap: break-word;
  white-space: normal;
  margin: 0;
}

div {
  word-wrap: break-word;
  white-space: normal;
  box-sizing: border-box;
}

iframe {
  border: none;
}

input::-ms-clear {
  display: none;
}

button,
input,
optgroup,
select,
textarea,
pre {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

body {
  font-size: 16px;
  font-family: 'Montserrat', Arial, Helvetica, Sans-Serif;
  box-sizing: border-box;
  color: #333;
  background-color: #fff;
  background-position: top center;
  background-size: cover;
}

span, a, p, strong, em, b {
  line-height: 1.5;
}

a {
  color: inherit;
}

.container {
  max-width: 1188px;
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;
}

*:focus, *:focus-visible {
  outline: none !important;
}

h1, h2, h3, h4, h4, h6, .h1-title, .h2-title, .h3-title, .h4-title, .h5-title, .h6-title {
  display: block;
  font-weight: 700;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.wrapper:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 998;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transition-property: opacity, visibility;
}

.main {
  flex: 1 0 auto;
}

.btn-full-reset {
  padding: 0;
  border: 0;
  background-color: transparent;
}

input[type='text'] {
  background: none;
  border: none;
  padding: 0;
}

.input {
  padding: 5px 6px;
  padding-left: 6px;
  border: 1.5px solid #e7eaee;
  background: #fff;
  border-radius: 6px;
  line-height: 30px;
  font-size: 16px;
  width: auto;
  max-width: 170px;
  line-height: 20px;
  padding-left: 28px;
}

.input--small {
  line-height: 20px;
  max-width: 170px;
}

.hidden {
  display: none !important;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.disable {
  pointer-events: none !important;
}

.lang {
  position: relative;
}

.lang__active-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 5px;
  color: #fff;
  text-transform: uppercase;
  transition-property: color;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}

@media (any-hover: hover) {
  .lang__active-item:hover {
    color: #173967;
  }
}

.lang__active-item i {
  font-size: 10px;
  transition-property: transform;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  position: relative;
  top: -2px;
}

@media only screen and (min-width: 1200px) {
  .lang__active-item i {
    color: #173967;
  }
}

@media only screen and (min-width: 1200px) {
  .lang__active-item {
    color: #333;
  }
}

.lang__active-item--active i {
  transform: scaleY(-1);
}

@media only screen and (min-width: 1200px) {
  .lang__active-item--active {
    color: #fff !important;
  }
  .lang__active-item--active i {
    color: #fff;
  }
}

.lang__list {
  top: calc(100% + 24px);
  position: absolute;
  right: 0;
  background: #fff;
  box-shadow: 2px 2px 16px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  overflow: hidden;
  grid-auto-flow: column;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(159px, 1fr));
  grid-template-rows: repeat(14, auto);
  grid-auto-columns: 159px;
  transform: scale(0);
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transition-property: visibility, opacity, transform;
  transform-origin: top right;
}

@media only screen and (min-width: 1200px) {
  .lang__list {
    top: calc(100% + 34px);
    grid-template-rows: repeat(10, auto);
  }
}

.lang__list--open {
  transform: scale(1);
}

.lang__list:before {
  content: "";
  position: absolute;
  top: -10px;
  border-left: 18px solid transparent;
  border-right: 18px solid transparent;
  border-bottom: 11px solid #fff;
  left: calc(93% - 18px);
}

.lang__active-item, .lang__list {
  z-index: 999;
}

@media only screen and (max-width: 1200px) {
  .lang__item:not(:nth-child(14 + n)) a {
    border-right: 1px solid #e7eaee;
  }
}

@media only screen and (min-width: 1200px) {
  .lang:not(:nth-child(24 + n)) a {
    border-right: 1px solid #e7eaee;
  }
}

.lang a {
  color: #173967;
  display: flex;
  align-items: center;
  gap: 9px;
  border-right: 1px solid #e7eaee;
  transition-property: background;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  padding: 11px 16px;
}

@media (any-hover: hover) {
  .lang a:hover {
    background: #e8f8ff;
  }
}

@media only screen and (max-width: 1200px) {
  .header {
    background: #173967;
  }
}

.header__logo {
  margin-right: auto;
}

.header__logo img {
  height: auto;
}

@media only screen and (min-width: 1200px) {
  .header__logo img {
    width: auto;
    height: 41px;
  }
}

@media only screen and (max-width: 1200px) {
  .header__container--desktop {
    max-width: none;
    width: auto;
    padding: 0;
    margin: 0;
  }
}

@media only screen and (min-width: 1200px) {
  .header__container--mob {
    max-width: none;
    width: auto;
    padding: 0;
    margin: 0;
  }
}

.header__row {
  padding-top: 7px;
  padding-bottom: 7px;
  flex-grow: 1;
  gap: 25px;
  display: flex;
  align-items: center;
}

@media only screen and (min-width: 1200px) {
  .header__nav {
    background: #173967;
  }
}

.header__burger {
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 2;
  color: #fff;
}

@media only screen and (min-width: 1200px) {
  .header__burger {
    display: none;
  }
}

.header__burger--active {
  z-index: 1001;
  color: #333;
}

.header__burger--active .header__burger-wrapper span {
  transform: scaleX(0);
}

.header__burger--active .header__burger-wrapper:before {
  top: calc(50% - 2px);
  transform: rotate(45deg);
}

.header__burger--active .header__burger-wrapper:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.header__burger-wrapper {
  position: relative;
  width: 20px;
  height: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header__burger-wrapper:before, .header__burger-wrapper:after,
.header__burger-wrapper span {
  width: 100%;
  height: 2px;
  background: currentColor;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transition-property: transform, top, bottom, backgorund;
}

.header__burger-wrapper:before, .header__burger-wrapper:after {
  content: '';
  position: absolute;
  left: 0px;
}

.header__burger-wrapper:before {
  top: 0px;
}

.header__burger-wrapper:after {
  bottom: 0px;
}

.header__menu {
  display: flex;
}

@media only screen and (min-width: 1200px) {
  .header__menu {
    position: relative;
  }
}

@media only screen and (max-width: 1200px) {
  .header__menu {
    flex-direction: column;
    padding: 37px 20px 0;
    background: #fff;
    max-height: 100vh;
    position: fixed;
    z-index: 1000;
    top: 0;
    right: 0;
    width: 70%;
    height: 100%;
    overflow: auto;
    transform: scale(0);
    transform-origin: top right;
    opacity: 0;
    visibility: hidden;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
    transition-property: transform, opacity, visibility;
  }
}

.header__menu--open {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.header__search-wrapper {
  position: relative;
}

@media only screen and (max-width: 1200px) {
  .header__search-wrapper {
    display: none;
  }
}

.header__search {
  position: relative;
  transition-property: border-color;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}

.header__search:has(input:focus) {
  border-color: #173967;
}

.header__search.input--small input {
  line-height: 20px;
}

.header__search input {
  line-height: 30px;
}

.header__search--mob {
  width: 100%;
  max-width: 100%;
}

@media only screen and (min-width: 1200px) {
  .header__search--mob {
    display: none;
  }
}

.header__search--desktop {
  position: absolute;
  right: 0;
  top: 50%;
  top: 50%;
  transform: translateY(-50%);
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transition-property: max-width, visibility, opacity;
}

.header__search--desktop:not(.header__search--open) {
  max-width: 0;
}

.header__search-btn {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 28px;
  height: 30px;
  padding: 5px;
}

@media only screen and (min-width: 1200px) {
  .header__menu-list {
    display: flex;
    gap: 75px;
    align-items: center;
    color: #fff;
  }
}

@media (any-hover: hover) {
  .header__menu-item:hover .header__sub-menu {
    display: grid;
  }
  .header__menu-item:hover .header__menu-link:has(+ .header__sub-menu):before {
    display: block;
  }
  .header__menu-item:hover .header__menu-link i {
    transform: scaleY(-1);
  }
}

.header__menu-link {
  padding: 14px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.125;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transition-property: border-color, font-weight;
  position: relative;
  z-index: 1;
  gap: 5px;
}

@media only screen and (max-width: 1200px) {
  .header__menu-link {
    border-bottom: 1px solid #e7eaee;
  }
}

.header__menu-link i {
  transition-property: transform;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  font-size: 12px;
}

@media only screen and (max-width: 1200px) {
  .header__menu-link i {
    color: #173967;
  }
}

.header__menu-link:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 7px solid #fff;
  width: 20px;
  height: 7px;
  transform: translateX(10px);
  display: none;
}

@media only screen and (max-width: 1200px) {
  .header__menu-link--active {
    border-color: transparent;
    font-weight: 600;
  }
  .header__menu-link--active i {
    transform: scaleY(-1);
  }
}

.header__sub-menu-col {
  margin-top: 14px;
}

.header__sub-menu-title {
  font-weight: 600;
}

@media only screen and (min-width: 1200px) {
  .header__sub-menu-title {
    padding-left: 30px;
    padding-right: 30px;
    text-transform: uppercase;
  }
}

.header__sub-menu {
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transition-property: max-height, visibility, opacity;
}

@media only screen and (max-width: 1200px) {
  .header__sub-menu {
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    max-height: 0;
  }
}

@media only screen and (min-width: 1200px) {
  .header__sub-menu {
    display: none;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    grid-auto-columns: 290px;
    gap: 20px;
    background: #fff;
    color: #333;
    border-radius: 6px;
    filter: drop-shadow(2px 2px 16px rgba(0, 0, 0, 0.25));
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 0;
    grid-auto-flow: column;
    top: calc(100% - 1px);
  }
}

@media only screen and (max-width: 1200px) {
  .header__sub-menu--open {
    max-height: 999px;
    opacity: 1;
    visibility: visible;
  }
}

.header__sub-menu-list:not(:first-child) {
  margin-top: 10px;
}

.header__sub-menu-item {
  font-size: 14px;
  line-height: 1.214;
}

@media (any-hover: hover) {
  .header__sub-menu-item:hover {
    background: #e8f8ff;
  }
}

@media only screen and (min-width: 1200px) {
  .header__sub-menu-item {
    font-size: 15px;
    padding: 0 10px;
  }
}

.header__sub-menu-item a {
  display: flex;
  align-items: center;
  gap: 5px;
  border-bottom: 1px solid #e7eaee;
  padding: 10px;
  padding-left: 0;
}

@media only screen and (min-width: 1200px) {
  .header__sub-menu-item a {
    padding: 19px 20px;
  }
}

.header__sub-menu-item i {
  color: #173967;
}
